import React from 'react';
import ImageLoader from '../common/ImageLoader'
import LazyLoad from 'react-lazyload';
import formatDate from '../common/dateFormatter';

const htmlTags = /<(.|\n)*?>/g


export default function ArticleBriefing(props) {
    const figRef = React.createRef()

    return (
        <div className='article-big briefing'>
                    <div className='category'><p>
                Daily Briefing
            </p></div>

            <LazyLoad
                placeholder={<div className='img-container'></div>}
                debounce={false}
                offsetVertical={300}
                overflow={false}

            >
                <a href={props.post.link}><figure className='img-container'>
                
                
                <ImageLoader media={props.post.jetpack_featured_media_url} />
                
                </figure></a>
            </LazyLoad>


        
        
    
            <div className='info'>
                <h2><a href={props.post.link}>{props.post.title.rendered}</a></h2>
                <p className='summary'>{props.post.excerpt.rendered.replace(htmlTags, '')}</p>
                <p className='article-meta'>
                <a href={props.authorLink}>{props.author}</a>               <time dateTime={props.post.date}>{formatDate(props.post.date)}</time></p>

            </div>
        </div>
    );
}


